import Vue from 'vue';
import App from './App.vue';
import router from './router';
import store from './store';
import VeeValidate, { Validator, } from 'vee-validate';

Vue.use(VeeValidate);

Vue.config.productionTip = false;

Validator.localize('ru', {
  messages: {
    required: '  ',
    min_value: () => '        ',
    min: () => ' ',
    numeric: () => '    ',
    email: () => ' '
  }
});

new Vue({
  router,
  store,
  render: (h) => h(App),
}).$mount('#app');
